*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100%;
  height: 100%;
  background-color: #f2f5f7;
  font-family: Inter, sans-serif;
}

main {
  min-height: 100vh;
  padding: 10px 20px;
}

footer {
  height: 50px;
  color: #242424;
  background: #7c7c7c2b;
}

.search {
  width: 50%;
  justify-content: end;
  align-items: center;
  display: flex;
}

button {
  height: 44px;
  width: 44px;
  color: #fff;
  cursor: pointer;
  background: #7c7c7c2b;
  border: none;
  border-radius: 50%;
  outline: none;
  margin: .5em;
  transition: all .2s ease-in-out;
}

input.search-bar {
  color: #fff;
  width: calc(100% - 100px);
  background: #a4a4a42b;
  border: none;
  border-radius: 24px;
  outline: none;
  padding: .5em 1em;
  font-family: inherit;
  font-size: 1.25rem;
}

button:hover {
  background: #7c7c7c6b;
}

.card {
  max-width: 100%;
  transform-style: preserve-3d;
  background-color: #fff;
  border-radius: 10px;
  flex-direction: column;
  flex: 0 0 300px;
  margin: 1rem;
  transition: transform .4s, background-color .4s;
  display: flex;
  overflow: hidden;
  transform: translate3d(0, 0, 0)scale3d(1, 1, 1)rotateX(0)rotateY(0)rotateZ(0)skew(0deg);
  box-shadow: 0 5px 10px #00000026;
}

.card:hover {
  background-color: #81818b26;
  transform: translate3d(0, -8px, 0)scale3d(1, 1, 1)rotateX(0)rotateY(0)rotateZ(0)skew(0deg);
}

.card-img {
  padding-bottom: 56.25%;
  position: relative;
}

.card-img img {
  width: 100%;
  position: absolute;
}

.card-body {
  padding: 1.5rem;
}

.card-title {
  font-size: 1.25rem;
  font-weight: 700;
  line-height: 1.33;
}

.card-title.skeleton {
  min-height: 28px;
  border-radius: 4px;
}

.card-intro {
  margin-top: .75rem;
  line-height: 1.5;
}

.card-intro.skeleton {
  min-height: 72px;
  border-radius: 4px;
}

.skeleton {
  background-color: #e2e5e7;
  background-image: linear-gradient(90deg, #fff0, #ffffff80, #fff0);
  background-position: -40px 0;
  background-repeat: no-repeat;
  background-size: 40px 100%;
  animation: 1s infinite shine;
}

@keyframes shine {
  to {
    background-position: right -40px top 0;
  }
}

.navmenu {
  width: 100%;
  background: #303030;
  justify-content: space-between;
  padding: 5px 20px;
  display: flex;
}

h1 {
  color: #ccc;
  align-items: center;
  display: flex;
}

.filter-container {
  justify-content: end;
  gap: 2rem;
  display: flex;
}

.select {
  width: 100%;
  -webkit-appearance: none;
  appearance: none;
  background: #fff url("../down-arrow-download-svgrepo-com.0c3e357d.svg") 95% 60% no-repeat;
  border: none;
  border-radius: 10px;
  outline: purple;
  padding: 10px 8px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
}

.filter-wrapper {
  width: 200px;
}

.filterName {
  color: #000;
  font-size: .75rem;
  font-weight: 500;
  line-height: 1.8;
}

.news-container {
  grid-gap: 10px;
  grid-template-columns: auto auto;
  display: grid;
}

@media screen and (min-width: 768px) {
  .news-container {
    grid-gap: 10px;
    grid-template-columns: auto auto auto;
    display: grid;
  }
}

@media screen and (min-width: 1280px) {
  .news-container {
    grid-gap: 10px;
    grid-template-columns: auto auto auto auto;
    display: grid;
  }
}

